import axios from "axios";
const url = "api/posts/";

class PostService {
	//Get from API
	static getPosts() {
		return new Promise((resolve, reject) => {
			axios
				.get(url)
				.then((res) => {
					const data = res.data;
					resolve(
						data.map((post) => ({
							...post,
							createdAt: new Date(post.createdAt),
						}))
					);
				})
				.catch((err) => {
					reject(err);
				});
		});
	}

	// Create Post
	static insertPost(msg) {
		return axios.post(url, {
			message: msg,
		});
	}
	// Delete post
	static deletePost(id) {
		return axios.delete(`${url}${id}`);
	}
}

export default PostService;
