<template>
	<div class="text-center leading-loose">
		<h1 class="text-4xl ">Quick & Easy List App</h1>
		<input
			v-model="text"
			placeholder="edit me"
			class="placeholder-gray-600 focus:placeholder-gray-500 border p-1 m-1"
		/>
		<button
			v-on:click="addMessage"
			class="bg-blue-500 hover:bg-blue-700 text-white font-bold px-4 rounded-full "
		>
			Add
		</button>
		<p v-if="error">{{ error }}</p>
		<div
			class="max-w-sm rounded-full overflow-hidden shadow-lg mx-auto m-3 bg-gray-200"
			v-for="post in posts"
			:key="post._id"
			v-on:dblclick="deletePost(post._id)"
		>
			<p class="font-bold text-xl mb-2">{{ post.message }}</p>
			<span class="text-gray-700 text-base"
				>created at : {{ post.createdAt.getDate() }}/{{
					post.createdAt.getMonth() + 1
				}}</span
			>
		</div>
	</div>
</template>

<script>
import PostService from "../postService";

export default {
	name: "MessageComponent",
	data() {
		return {
			posts: [],
			error: "",
			text: "",
		};
	},
	async created() {
		try {
			this.posts = await PostService.getPosts();
		} catch (error) {
			this.error = error.message;
		}
	},
	methods: {
		async addMessage() {
			await PostService.insertPost(this.text);
			this.posts = await PostService.getPosts();
		},
		async deletePost(id) {
			await PostService.deletePost(id);
			this.posts = await PostService.getPosts();
		},
	},
};
</script>

